@import "mixins";

body {
	margin: 0;
	padding: 0; }
.container {
	margin: auto;
	font-family: Sans-serif; }


.mail-header {
	border-color: $main-blue;
	background-image: url('https://westwebdev.github.io/wsg-mail//img/fon.png');
	background-position: top center;
	background-repeat: no-repeat;
	background-size: cover;

	text-align: center;
	.logo {
		background: $white;
		display: inline-block;
		padding: 22px 38px;
		// width: 161px
		height: 142px;
		img {
			height: 100%; } }
	.header-text {
		color: $white;
		font-size: 21px;
		font-weight: bold;
		margin: 15px auto 45px; }
	.header-caption {
		display: inline-block;
		padding: 10px;
		border: 4px solid $white; }
	.line {
		height: 4px;
		width: 25%;
		background-color: $white;
		display: inline-block;
		margin-bottom: 25px; }
	.header-content {
		background-color: $white;
		background-size: 100% 100%;
		font-weight: bold;
		padding: 35px 0;
		display: inline-block;
		padding: 30px;
		.header-h2 {
			font-size: 23px;
			color: #5b5b5b;
			max-width: $mw;
			margin: auto; }
		.header-h1 {
			font-size: 40px;
			max-width: $mw;
			margin: auto; } }
	.social {
		a {
			display: inline-block;
			width: 40px;
			height: 40px;
			margin: 20px 16px;
			img {
				height: 100%; } } } }

.contact {
	max-width: $mw;
	margin: auto;
	text-align: center;
	padding-bottom: 15px;
	padding-left: 35px;
	padding-right: 15px;

	.phone {
		display: inline-block;
		font-weight: bold;
		font-size: 20px;
		padding: 20px;
		div {
			display: inline-block; } }
	.web {
		// margin-left: 20px
		display: inline-block;
		padding-top: 10px;
		padding-bottom: 10px;
		padding-left: 35px;
		padding-right: 15px;
		background-color: $main-blue;
		color: $white;
		font-size: 18px;
		vertical-align: middle;
		a {
			text-decoration: none;
			color: $white;
			display: block; }
		.site {
			font-weight: bold; } } }

.text-container {
	max-width: $mw;
	margin: auto;
	font-size: 22px;
	text-align: center;

	.block-left {
		max-width: 585px;
		display: inline-block;
		margin: auto;
		padding-left: 5px;
		div {
			text-align: left; }
		.block-h {
			color: $main-blue;
			font-weight: bold;
			font-size: 26px; } }

	.block-li {
		margin: 0;
		margin-top: 5px;
		margin-bottom: 25px;
		padding-left: 25px;
		li {
			margin: 3px 0; } }

	.block-right {
		max-width: 290px;
		display: inline-block;
		margin: auto;
		text-align: center;
		vertical-align: top;
		// float: right
		div {
			text-align: left; }
		img {
			text-align: right;
			width: 100%; }
		.block-h-right {
			font-weight: bold;
			span {
				color: $main-blue; } }
		.people {
			// float: left
			margin-top: -35px;
			// margin-right: 40px
			// margin-left: -40px
			img {
				margin-left: -120px;
				width: 80%; } }

		.text-block-right {
 } } }			// margin-top: -110px

.image {
	max-width: $mw;
	margin: auto;
	text-align: center;
	margin-bottom: 30px;
	.image-img {
		display: inline-block;
		width: 290px;
		img {
			width: 100%; } } }

.mail-footer {
	background-color: $main-blue;
	background-image: url('https://westwebdev.github.io/wsg-mail//img/footer.png');
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	padding: 70px 0 50px;
	.footer-main-text {
		max-width: $mw;
		margin: auto;
		font-size: 33px;
		color: $white;
		text-align: center;
		margin-top: 50px; }
	.footer-main-text-2 {
		font-size: 18px;
		margin-top: 260px; }
	.adress {
		font-weight: bold;
		margin-bottom: 5px; } }
